<template>
    <div id="view-employee">
        <ul class="collection with-header bord">
            <li class="collection-header opaque-bg">
                <h4>
                    {{name}}
                </h4>
            </li>
            <li class="collection-item opaque-bg">
                Employee ID#: {{employee_id}}
            </li>
            <li class="collection-item opaque-bg">
                Address: {{address}}
            </li>
            <li class="collection-item opaque-bg">
                Contact Number: {{contact_no}}
            </li>
            <li class="collection-item opaque-bg">
                Course: {{course}}
            </li>
            <li class="collection-item opaque-bg">
                Course Major: {{course_major}}
            </li>
            <li class="collection-item opaque-bg">
                Mother's Name: {{mother_name}}
            </li>
            <li class="collection-item opaque-bg">
                Father's Name: {{father_name}}
            </li>
            <li class="collection-item opaque-bg">
                Emergency Contact Person: {{contact_person}}
            </li>
            <li class="collection-item opaque-bg">
                Emergency Contact Number: {{person_num}}
            </li>
            <li class="collection-item opaque-bg">
                LET Rating: {{let_rating}}
            </li>
            <li class="collection-item opaque-bg">
                SSS Number: {{sss_no}}
            </li>
            <li class="collection-item opaque-bg">
                PhilHealth Number: {{philhealth_no}}
            </li>
            <li class="collection-item opaque-bg">
                PAG-IBIG Number: {{pagibig_no}}
            </li>
            <li class="collection-item opaque-bg">
                TIN Number: {{tin_no}}
            </li>
        </ul>
        <div class="view-options">
            <button @click="deleteEmployee" class="btn btn-large red">Delete</button>
            <router-link to="/e" class="btn btn-large grey">Back</router-link>
        </div>
        
       
        <div class="fixed-action-btn">
            <router-link v-bind:to="{name: 'edit-employee', params: {employee_id: employee_id}}" class="btn-floating btn-large bg-icon">
                <i class="fa fa-pencil"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseinit'
export default {
    name: 'view-employee',
    data () {
        return {
            employee_id: null,
            name: null,
            address: null,
            contact_no: null,
            course: null,
            course_major: null,
            mother_name: null,
            father_name: null,
            contact_person: null,
            person_num: null,
            let_rating: null,
            sss_no: null,
            philhealth_no: null,
            pagibig_no: null,
            tin_no: null
        }
    },
    // navigation guard : before page loads
    beforeRouteEnter(to, from, next) {
        db.collection('employees').where('employee_id', '==', to.params.employee_id).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.employee_id = doc.data().employee_id
                    vm.name = doc.data().name
                    vm.address = doc.data().address
                    vm.contact_no = doc.data().contact_no
                    vm.course = doc.data().course
                    vm.course_major = doc.data().course_major
                    vm.mother_name = doc.data().mother_name
                    vm.father_name = doc.data().father_name
                    vm.contact_person = doc.data().contact_person
                    vm.person_num = doc.data().person_num
                    vm.let_rating = doc.data().let_rating
                    vm.sss_no = doc.data().sss_no
                    vm.philhealth_no = doc.data().philhealth_no
                    vm.pagibig_no = doc.data().pagibig_no
                    vm.tin_no = doc.data().tin_no
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData() {
            db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach( doc => {
                    this.employee_id = doc.data().employee_id
                    this.name = doc.data().name
                    this.address = doc.data().address
                    this.contact_no = doc.data().contact_no
                    this.course = doc.data().course
                    this.course_major = doc.data().course_major
                    this.mother_name = doc.data().mother_name
                    this.father_name = doc.data().father_name
                    this.contact_person = doc.data().contact_person
                    this.person_num = doc.data().person_num
                    this.let_rating = doc.data().let_rating
                    this.sss_no = doc.data().sss_no
                    this.philhealth_no = doc.data().philhealth_no
                    this.pagibig_no = doc.data().pagibig_no
                    this.tin_no = doc.data().tin_no
                })
            })
        },
        deleteEmployee() {
            if(confirm('Are you sure you want to delete this employee?')) {
                db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach( doc => {
                        doc.ref.delete()
                        this.$router.push('/e')
                    })
                })
            }
        }
    }
}
</script>


