<template>
    <div id="teacher-list">
        <ul class="collection with-header emp-top list-box">
            <li class="collection-header opaque-bg">
                <h4 style="margin: 5px;">Teacher List</h4>
            </li>
            <!-- bind with firebase id -->
            <li v-for="employee in employees" v-bind:key="employee.id" class="collection-item opaque-bg">
                <div class="chip">{{employee.employee_id}}</div>
                {{ employee.name }}
                
                <router-link class="secondary-content view-icon" v-bind:to="{name:'view-employee', params: {employee_id: employee.employee_id}}">
                    <i class="fa fa-eye"></i> View
                </router-link>
            </li>
        </ul>

        <div class="fixed-action-btn">
            <router-link to="/e/new" class="btn-floating btn-large bg-icon">
                <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import firebase from 'firebase'
import db from './firebaseinit'
export default {
    name: 'teacher-list',
    data () {
        return {
            employees: []
        }
    },
    created () {
        db.collection('employees').orderBy('name').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                    'employee_id' : doc.data().employee_id,
                    'name' : doc.data().name,
                    'address' : doc.data().address,
                    'contact_no' : doc.data().contact_no,
                    'course' : doc.data().course,
                    'course_major' : doc.data().course_major,
                    'mother_name' : doc.data().mother_name,
                    'father_name' : doc.data().father_name,
                    'contact_person' : doc.data().contact_person,
                    'person_num' : doc.data().person_num,
                    'let_rating' : doc.data().let_rating,
                    'sss_no' : doc.data().sss_no,
                    'philhealth_no' : doc.data().philhealth_no,
                    'pagibig_no' : doc.data().pagibig_no,
                    'tin_no' : doc.data().tin_no
                }
                this.employees.push(data)
            })
        })
    },
    methods: {
        logout () {
            firebase.auth().signOut().then(() => {
                this.$router.replace('login')
            })
        }
    }
}
</script>


