<template>
    <div id="view-student">
        <ul class="collection with-header bord">
            <li class="collection-header opaque-bg">
                <h4>
                    {{fname}} {{mname}} {{lname}}
                </h4>
            </li>
            <li class="collection-item opaque-bg">
                Student ID#: {{student_id}}
            </li>
            <li class="collection-item opaque-bg">
                Grade Level: {{grade_level}}
            </li>
            <li class="collection-item opaque-bg">
                School Year: {{school_year}}
            </li>
            <li class="collection-item opaque-bg">
                Gender: {{gender}}
            </li>
            <li class="collection-item opaque-bg">
                Birthday: {{birthday}}
            </li>
            <li class="collection-item opaque-bg">
                Birthplace: {{birthplace}}
            </li>
            <li class="collection-item opaque-bg">
                Mother's Name: {{mother_name}}
            </li>
            <li class="collection-item opaque-bg">
                Mother's Contact Number: {{mother_no}}
            </li>
            <li class="collection-item opaque-bg">
                Mother's Occupation: {{mother_occupation}}
            </li>
            <li class="collection-item opaque-bg">
                Father's Name: {{father_name}}
            </li>
            <li class="collection-item opaque-bg">
                Father's Contact Number: {{father_no}}
            </li>
            <li class="collection-item opaque-bg">
                Father's Occupation: {{father_occupation}}
            </li>
            <li class="collection-item opaque-bg">
                Religion: {{religion}}
            </li>
            <li class="collection-item opaque-bg">
                LRN Number: {{lrn_no}}
            </li>
        </ul>
        <div class="view-options">
            <button @click="deleteStudent" class="btn btn-large red">Delete</button>
            <router-link to="/s" class="btn btn-large grey">Back</router-link>
        </div>
        
        <div class="fixed-action-btn">
            <router-link v-bind:to="{name: 'edit-student', params: {student_id: student_id}}" class="btn-floating btn-large bg-icon">
                <i class="fa fa-pencil"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseinit'
export default {
    name: 'view-student',
    data () {
        return {
            student_id: null,
            fname: null,
            mname: null,
            lname: null,
            gender: null,
            birthday: null,
            address: null,
            grade_level: null,
            birthplace: null,
            father_name: null,
            father_no: null,
            father_occupation: null,
            mother_name: null,
            mother_no: null,
            mother_occupation: null,
            lrn_no: null,
            religion: null,
            school_year: null
        }
    },
    // navigation guard : before page loads
    beforeRouteEnter(to, from, next) {
        db.collection('students').where('student_id', '==', to.params.student_id).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.student_id = doc.data().student_id
                    vm.fname = doc.data().fname
                    vm.mname = doc.data().mname
                    vm.lname = doc.data().lname
                    vm.gender = doc.data().gender
                    vm.address = doc.data().address
                    vm.grade_level = doc.data().grade_level
                    vm.birthday = doc.data().birthday
                    vm.birthplace = doc.data().birthplace
                    vm.father_name = doc.data().father_name
                    vm.father_no = doc.data().father_no
                    vm.father_occupation = doc.data().father_occupation
                    vm.mother_name = doc.data().mother_name
                    vm.mother_no = doc.data().mother_no
                    vm.mother_occupation = doc.data().mother_occupation
                    vm.lrn_no = doc.data().lrn_no
                    vm.religion = doc.data().religion
                    vm.school_year = doc.data().school_year            
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData() {
            db.collection('students').where('student_id', '==', this.$route.params.student_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach( doc => {
                    this.student_id = doc.data().student_id
                    this.fname = doc.data().fname
                    this.mname = doc.data().mname
                    this.lname = doc.data().lname
                    this.gender = doc.data().gender
                    this.birthday = doc.data().birthday
                    this.address = doc.data().address
                    this.grade_level = doc.data().grade_level
                    this.birthplace = doc.data().birthplace
                    this.father_name = doc.data().father_name
                    this.father_no = doc.data().father_no
                    this.father_occupation = doc.data().father_occupation
                    this.mother_name = doc.data().mother_name
                    this.mother_no = doc.data().mother_no
                    this.mother_occupation = doc.data().mother_occupation
                    this.lrn_no = doc.data().lrn_no
                    this.religion = doc.data().religion
                    this.school_year = doc.data().school_year            
                })
            })
        },
        deleteStudent() {
            if(confirm('Are you sure you want to delete this student record?')) {
                db.collection('students').where('student_id', '==', this.$route.params.student_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach( doc => {
                        doc.ref.delete()
                        this.$router.push('/s')
                    })
                })
            }
        }
    }
}
</script>


