<template>
    <div id="edit-employee">
        <h3 style="margin-left: 30%;">Update Teacher Record</h3>
        <div class="row">
            <form @submit.prevent="updateEmployee" class="col s12">
                <div class="row padding-fields">
                    <div class="input-field col s4" style="margin: 0px;">
                        <p class="col edit-field" style="margin: 0px; padding: 0px;">Employee Name</p>
                        <input type="text" v-model="name" required>
                    </div>

                    <div class="input-field col s3" style="margin: 0px;">
                        <p class="col edit-field" style="margin: 0px; padding: 0px;">Employee ID</p>
                        <input type="text" v-model="employee_id" required>
                    </div>

                    <div class="input-field col s3" style="margin: 0px;">
                        <p class="col edit-field" style="margin: 0px; padding: 0px;">Contact Number</p>
                        <input type="text" v-model="contact_no" required>
                    </div>

                    <div class="input-field col s2" style="margin: 0px; padding: 0px;">
                        <p class="col edit-field" style="margin: 0px;">LET Rating</p>
                        <input type="text" v-model="let_rating" required>
                    </div>
                </div>

                <div class="row padding-fields">
                    <div class="input-field col s3" style="margin: 0px;">
                        <p class="col edit-field" style="margin: 0px; padding: 0px;">Course</p>
                        <input type="text" v-model="course" required>
                    </div>

                    <div class="input-field col s3" style="margin: 0px;">
                        <p class="col edit-field" style="margin: 0px; padding: 0px;">Major</p>
                        <input type="text" v-model="course_major" required>
                    </div>
                    
                    <div class="input-field col s6" style="margin: 0px;">
                        <p class="col edit-field" style="margin: 0px; padding: 0px;">Address</p>
                        <input type="text" v-model="address" required> 
                    </div>
                </div>

                <div class="row padding-fields">

                    <div class="input-field col s3" style="margin: 0px;">
                        <p class="col edit-field" style="margin: 0px; padding: 0px;">Emergency Contact</p>
                        <input type="text" v-model="contact_person" required>
                    </div>
                    
                    <div class="input-field col s3" style="margin: 0px;">
                        <p class="col edit-field" style="margin: 0px; padding: 0px;">Emergency Contact Number</p>
                        <input type="text" v-model="person_num" required>
                    </div>

                    <div class="input-field col s3" style="margin: 0px;">
                        <p class="col edit-field" style="margin: 0px; padding: 0px;">Mother's Name</p>
                        <input type="text" v-model="mother_name" required>
                    </div>
                    
                    <div class="input-field col s3" style="margin: 0px;">
                        <p class="col edit-field" style="margin: 0px; padding: 0px;">Father's Name</p>
                        <input type="text" v-model="father_name" required>
                    </div>
                </div> 

                <div class="row padding-fields">
                    <div class="input-field col s3" style="margin: 0px;">
                        <p class="col edit-field" style="margin: 0px; padding: 0px;">SSS Number</p>
                        <input type="text" v-model="sss_no" required>
                    </div>
                    
                    <div class="input-field col s3" style="margin: 0px;">
                        <p class="col edit-field" style="margin: 0px; padding: 0px;">PhilHealth Number</p>
                        <input type="text" v-model="philhealth_no" required>
                    </div>
                    
                    <div class="input-field col s3" style="margin: 0px;">
                        <p class="col edit-field" style="margin: 0px; padding: 0px;">Pagibing Number</p>
                        <input type="text" v-model="pagibig_no" required>
                    </div>
                    
                    <div class="input-field col s3" style="margin: 0px;">
                        <p class="col edit-field" style="margin: 0px; padding: 0px;">TIN Number</p>
                        <input type="text" v-model="tin_no" required>
                    </div>
                </div>

                <div class="view-options">
                    <button type="submit" class="btn btn-large bg-icon">Submit</button>
                    <router-link to="/e" class="btn btn-large grey">Cancel</router-link>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseinit'
export default {
    name: 'edit-employee',
    data () {
        return {
            employee_id: null,
            name: null,
            address: null,
            contact_no: null,
            course: null,
            course_major: null,
            mother_name: null,
            father_name: null,
            contact_person: null,
            person_num: null,
            let_rating: null,
            sss_no: null,
            philhealth_no: null,
            pagibig_no: null,
            tin_no: null
        }
    }, 
    // navigation guard : before page loads
    beforeRouteEnter(to, from, next) {
        db.collection('employees').where('employee_id', '==', to.params.employee_id).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.employee_id = doc.data().employee_id
                    vm.name = doc.data().name
                    vm.address = doc.data().address
                    vm.contact_no = doc.data().contact_no
                    vm.course = doc.data().course
                    vm.course_major = doc.data().course_major
                    vm.mother_name = doc.data().mother_name
                    vm.father_name = doc.data().father_name
                    vm.contact_person = doc.data().contact_person
                    vm.person_num = doc.data().person_num
                    vm.let_rating = doc.data().let_rating
                    vm.sss_no = doc.data().sss_no
                    vm.philhealth_no = doc.data().philhealth_no
                    vm.pagibig_no = doc.data().pagibig_no
                    vm.tin_no = doc.data().tin_no
                })
            })
        })
    },
    //watcher
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData() {
            db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach( doc => {
                    this.employee_id = doc.data().employee_id
                    this.name = doc.data().name
                    this.address = doc.data().address
                    this.contact_no = doc.data().contact_no
                    this.course = doc.data().course
                    this.course_major = doc.data().course_major
                    this.mother_name = doc.data().mother_name
                    this.father_name = doc.data().father_name
                    this.contact_person = doc.data().contact_person
                    this.person_num = doc.data().person_num
                    this.let_rating = doc.data().let_rating
                    this.sss_no = doc.data().sss_no
                    this.philhealth_no = doc.data().philhealth_no
                    this.pagibig_no = doc.data().pagibig_no
                    this.tin_no = doc.data().tin_no
                })
            })
        },
        updateEmployee() {
            db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach( doc => {
                    doc.ref.update({
                        employee_id: this.employee_id,
                        name: this.name,
                        address: this.address,
                        contact_no: this.contact_no,
                        course: this.course,
                        course_major: this.course_major,
                        mother_name: this.mother_name,
                        father_name: this.father_name,
                        contact_person: this.contact_person,
                        person_num: this.person_num,
                        let_rating: this.let_rating,
                        sss_no: this.sss_no,
                        philhealth_no: this.philhealth_no,
                        pagibig_no: this.pagibig_no,
                        tin_no: this.tin_no

                    }).then(() => {
                        this.$router.push({name: 'view-employee', params: {employee_id: this.employee_id}})
                    })
                })
            })
        }
    }
}
</script>


