<template>
    <div id="edit-student">
        <h3 style="margin-left: 30%;">Edit Student Record</h3>
        <div class="row">
            <form @submit.prevent="updateStudent" class="col s12">
                <div class="row">
                    <div class="input-field col s4">
                        <span class="edit-field" style="margin: 0px;">First Name</span>
                        <input type="text" v-model="fname" required>
                    </div>

                    <div class="input-field col s4">
                        <span class="edit-field" style="margin: 0px;">Middle Name</span>
                        <input type="text" v-model="mname" required>
                    </div>
            
                    <div class="input-field col s4">
                        <span class="edit-field" style="margin: 0px;">Last Name</span>
                        <input type="text" v-model="lname" required>
                    </div>
                </div>
                
                <div class="row form-field">
                    <div class="input-field col s3">
                        <span class="edit-field" style="margin: 0px;">Student ID Number</span>
                        <input type="text" v-model="student_id" required>
                    </div>
                    <div class="input-field col s3">
                        <span class="edit-field" style="margin: 0px;">School Year</span>
                        <input type="text" v-model="school_year" required>
                    </div>
                    <div class="input-field col s6">
                        <span class="edit-field" style="margin: 0px;">Address</span>
                        <input type="text" v-model="address" required>
                    </div>
                </div>

                <div class="row form-field">
                    <div class="input-field col s3">
                        <span class="edit-field" style="margin: 0px;">Gender</span>
                        <input type="text" v-model="gender" required>
                    </div>

                    <div class="input-field col s3">
                        <span class="edit-field" style="margin: 0px;">Birthday</span>
                        <input type="date" v-model="birthday" required>
                    </div>

                    <div class="input-field col s3">
                        <span class="edit-field" style="margin: 0px;">Birthplace</span>
                        <input type="text" v-model="birthplace" required>
                    </div>

                    <div class="input-field col s3">
                        <span class="edit-field" style="margin: 0px;">Grade Level</span>
                        <input type="text" v-model="grade_level" required>
                    </div>
                </div>
                
                <div class="row form-field">
                    <div class="input-field col s4">
                        <span class="edit-field" style="margin: 0px;">Father's Name</span>
                        <input type="text" v-model="father_name" required>
                    </div>
                    
                    <div class="input-field col s4">
                        <span class="edit-field" style="margin: 0px;">Father's Number</span>
                        <input type="text" v-model="father_no" required>
                    </div>

                    <div class="input-field col s4">
                        <span class="edit-field" style="margin: 0px;">Father's Occupation</span>
                        <input type="text" v-model="father_occupation" required>
                    </div>
                </div>
                
                <div class="row form-field">
                    <div class="input-field col s4">
                        <span class="edit-field" style="margin: 0px;">Mother's Name</span>
                        <input type="text" v-model="mother_name" required>
                    </div>
                    
                    <div class="input-field col s4">
                        <span class="edit-field" style="margin: 0px;">Mother's Number</span>
                        <input type="text" v-model="mother_no" required>
                    </div>

                    <div class="input-field col s4">
                        <span class="edit-field" style="margin: 0px;">Mother's Occupation</span>
                        <input type="text" v-model="mother_occupation" required>
                    </div>
                </div>
                
                
                <div class="row form-field">
                    <div class="input-field col s6">
                        <span class="edit-field" style="margin: 0px;">LRN Number</span>
                        <input type="text" v-model="lrn_no" required>
                    </div>

                    <div class="input-field col s6">
                        <span class="edit-field" style="margin: 0px;">Religion</span>
                        <input type="text" v-model="religion" required>
                    </div>
                </div>

                <div class="view-options">
                    <button type="submit" class="btn btn-large bg-icon">Submit</button>
                    <router-link to="/s" class="btn btn-large grey">Cancel</router-link>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseinit'
export default {
    name: 'edit-student',
    data () {
        return {
            student_id: null,
            fname: null,
            mname: null,
            lname: null,
            gender: null,
            birthday: null,
            address: null,
            grade_level: null,
            birthplace: null,
            father_name: null,
            father_no: null,
            father_occupation: null,
            mother_name: null,
            mother_no: null,
            mother_occupation: null,
            lrn_no: null,
            religion: null,
            school_year: null
        }
    }, 
    // navigation guard : before page loads
    beforeRouteEnter(to, from, next) {
        db.collection('students').where('student_id', '==', to.params.student_id).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.student_id = doc.data().student_id
                    vm.fname = doc.data().fname
                    vm.mname = doc.data().mname
                    vm.lname = doc.data().lname
                    vm.gender = doc.data().gender
                    vm.address = doc.data().address
                    vm.grade_level = doc.data().grade_level
                    vm.birthday = doc.data().birthday
                    vm.birthplace = doc.data().birthplace
                    vm.father_name = doc.data().father_name
                    vm.father_no = doc.data().father_no
                    vm.father_occupation = doc.data().father_occupation
                    vm.mother_name = doc.data().mother_name
                    vm.mother_no = doc.data().mother_no
                    vm.mother_occupation = doc.data().mother_occupation
                    vm.lrn_no = doc.data().lrn_no
                    vm.religion = doc.data().religion
                    vm.school_year = doc.data().school_year            
                })
            })
        })
    },
    //watcher
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData() {
            db.collection('students').where('student_id', '==', this.$route.params.student_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach( doc => {
                    this.student_id = doc.data().student_id
                    this.fname = doc.data().fname
                    this.mname = doc.data().mname
                    this.lname = doc.data().lname
                    this.gender = doc.data().gender
                    this.birthday = doc.data().birthday
                    this.address = doc.data().address
                    this.grade_level = doc.data().grade_level
                    this.birthplace = doc.data().birthplace
                    this.father_name = doc.data().father_name
                    this.father_no = doc.data().father_no
                    this.father_occupation = doc.data().father_occupation
                    this.mother_name = doc.data().mother_name
                    this.mother_no = doc.data().mother_no
                    this.mother_occupation = doc.data().mother_occupation
                    this.lrn_no = doc.data().lrn_no
                    this.religion = doc.data().religion
                    this.school_year = doc.data().school_year            
                })
            })
        },
        updateStudent() {
            db.collection('students').where('student_id', '==', this.$route.params.student_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach( doc => {
                    doc.ref.update({
                        student_id: this.student_id,
                        fname: this.fname,
                        mname: this.mname,
                        lname: this.lname,
                        gender: this.gender,
                        address: this.address,
                        grade_level: this.grade_level,
                        birthday: this.birthday,
                        birthplace: this.birthplace,
                        father_name: this.father_name,
                        father_no: this.father_no,
                        father_occupation: this.father_occupation,
                        mother_name: this.mother_name,
                        mother_no: this.mother_no,
                        mother_occupation: this.mother_occupation,
                        lrn_no: this.lrn_no,
                        religion: this.religion,
                        school_year: this.school_year
                    }).then(() => {
                        this.$router.push({name: 'view-student', params: {student_id: this.student_id}})
                    })
                })
            })
        }
    }
}
</script>


