<template>
    <div id="student-list">
        <ul class="collection with-header emp-top list-box">
            <li class="collection-header opaque-bg">
                <h4 style="margin: 5px;">Student List</h4>
            </li>
            <!-- bind with firebase id -->
            <li v-for="student in students" v-bind:key="student.id" class="collection-item opaque-bg">
                <div class="chip">{{student.grade_level}}</div>
                {{student.lname}}, {{student.fname}} {{student.mname}}      
                <router-link class="secondary-content view-icon" v-bind:to="{name:'view-student', params: {student_id: student.student_id}}">
                    <i class="fa fa-eye"></i> View
                </router-link>
            </li>
        </ul>

        <div class="fixed-action-btn">
            <router-link to="/s/new" class="btn-floating btn-large bg-icon">
                <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import firebase from 'firebase'
import db from './firebaseinit'
export default {
    name: 'student-list',
    data () {
        return {
            students: []
        }
    },
    created () {
        db.collection('students').orderBy('lname').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                    'student_id' : doc.data().student_id,
                    'fname' : doc.data().fname,
                    'mname' : doc.data().mname,
                    'lname' : doc.data().lname,
                    'gender' : doc.data().gender,
                    'birthday' : doc.data().birthday,
                    'address' : doc.data().address,
                    'grade_level' : doc.data().grade_level,
                    'birthplace' : doc.data().birthplace,
                    'father_name' : doc.data().father_name,
                    'father_no' : doc.data().father_no,
                    'father_occupation' : doc.data().father_occupation,
                    'mother_name' : doc.data().mother_name,
                    'mother_no' : doc.data().mother_no,
                    'mother_occupation' : doc.data().mother_occupation,
                    'lrn_no' : doc.data().lrn_no,
                    'religion' : doc.data().religion,
                    'school_year' : doc.data().school_year            
                }
                this.students.push(data)
            })
        })
    },
    methods: {
        logout () {
            firebase.auth().signOut().then(() => {
                this.$router.replace('login')
            })
        }
    }
}
</script>


