<template>
   <nav>
       <div class="nav-wrapper navtop">
           <div class="container">
               <img src="../assets/banner.jpeg" class="icon-top">
               <router-link to="/" class="brand-logo">Blessed Zion Academy of Cebu</router-link>
               <button class="about-link logout-btn" @click="logout">Logout</button>
               <router-link to="/s/" class="about-link">Student List</router-link>
               <router-link to="/e/" class="about-link">Employee List</router-link>
           </div>
       </div>
   </nav>
</template>


<script>
import firebase from 'firebase'
import db from './firebaseinit'
export default {
    methods: {
        logout () {
            firebase.auth().signOut()
            this.$router.replace('/login')
        }
    }
}
</script>



